import React from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import car1 from "../img/banner (1).jpeg";
import car2 from "../img/banner (2).JPG";
import car3 from "../img/banner (3).JPG";
import a1 from "../img/a1 (1).jpeg";
import a2 from "../img/a1 (2).jpeg";
import a3 from "../img/a1 (3).jpeg";
import a4 from "../img/a1 (4).jpeg";
import a5 from "../img/aa.jpeg"

import "./Carousel.css"; // Import custom styles for Carousel

const Carousel = () => {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 5000, // Change every 5 seconds
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          infinite: true,
          dots: true
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          initialSlide: 1
        }
      }
    ]
  };

  return (
    <Slider {...settings} className="full-screen-carousel">
      <div>
        <img className="ff" src={car1} alt="Slide 1" />
      </div>
      <div>
        <img className="ff" src={car2} alt="Slide 2" />
      </div>
      <div>
        <img className="ff" src={car3} alt="Slide 3" />
      </div>
      <div>
        <img className="ff" src={a1} alt="Slide 4" />
      </div>
      <div>
        <img className="ff" src={a2} alt="Slide 5" />
      </div>
      <div>
        <img className="ff" src={a3} alt="Slide 6" />
      </div>
      <div>
        <img className="ff" src={a4} alt="Slide 7" />
      </div>

      <div>
        <img className="ff" src={a5} alt="Slide 7" />
      </div>
    </Slider>
  );
};

export default Carousel;
