import React from "react";
import img1 from "../img/nsd6.jpg";
import img2 from "../img/nsd4.jpg";
import img3 from "../img/g4.jpg";
import img4 from "../img/g11.jpg";

const Event = () => {
  const events = [
    { name: "3D model lab", imageSrc: img1, description: "Explore the 3D model lab at our college." },
    { name: "Bio lab", imageSrc: img2, description: "Discover the wonders of biology in our Bio lab." },
    { name: "Cognizant", imageSrc: img3, description: "Cognizant event showcasing innovations and technologies." },
    { name: "Medical Checkup", imageSrc: img4, description: "Regular medical checkups for student well-being." },
  ];

  return (
    <div className="px-4 py-16 mx-auto sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-8 lg:py-20" data-aos="fade-up">
      <div className="text-center mb-10 lg:mb-12">
        <h2 className="max-w-lg mx-auto mb-5 font-sans text-3xl font-bold tracking-tight text-blue-700 sm:text-4xl sm:leading-none md:mb-6 group">
          <span className="inline-block mb-1 sm:mb-4">
            Explore the Artistic Experience
            <br className="hidden md:block" />
            at Our College Events
          </span>
          <div className="h-1 mx-auto duration-300 origin-left transform bg-deep-purple-accent-400 scale-x-30 group-hover:scale-x-100" />
        </h2>
        <p className="text-gray-700 lg:text-base lg:max-w-md mx-auto">
          Immerse yourself in the beauty of our college events, showcasing diverse forms of art and creativity.
        </p>
      </div>
      <div className="grid gap-6 row-gap-5 mb-8 lg:grid-cols-4 sm:row-gap-6 sm:grid-cols-2">
        {events.map((event, index) => (
          <div key={index} data-aos="fade-up" data-aos-delay={`${index * 100}`}>
            <a href="/" aria-label={`View ${event.name}`}>
              <div className="relative overflow-hidden transition duration-200 transform rounded shadow-lg hover:-translate-y-2 hover:shadow-2xl">
                <img className="object-cover w-full h-56 md:h-64 xl:h-80" src={event.imageSrc} alt={event.name} />
                <div className="absolute inset-0 px-6 py-4 transition-opacity duration-200 bg-black bg-opacity-75 opacity-0 hover:opacity-100">
                  <p className="mb-4 text-lg font-bold text-gray-100">{event.name}</p>
                  <p className="text-sm tracking-wide text-gray-300">{event.description}</p>
                </div>
              </div>
            </a>
          </div>
        ))}
      </div>
      <div data-aos="fade-up" className="text-center">
        <a href="/campus" aria-label="View Gallery" className="inline-flex items-center font-semibold transition-colors duration-200 text-deep-purple-accent-400 hover:text-deep-purple-800">
          VIEW GALLERY
          <svg className="inline-block w-3 ml-2" fill="currentColor" viewBox="0 0 12 12">
            <path d="M9.707,5.293l-5-5A1,1,0,0,0,3.293,1.707L7.586,6,3.293,10.293a1,1,0,1,0,1.414,1.414l5-5A1,1,0,0,0,9.707,5.293Z" />
          </svg>
        </a>
      </div>
    </div>
  );
};

export default Event;
